<template>
    <v-container>

        <div v-if="events.length < 4">
            <v-card v-for="mainEvent in events" :key="mainEvent.id" style="margin-bottom: 20px;">
                <v-card-title>{{ mainEvent.mainTitle }}</v-card-title>
                <v-img :src="'/images/' + mainEvent.image"></v-img>
                <v-card-text>
                    
                    {{ mainEvent.mainDesc }}    
                </v-card-text>
                <v-card-text style="margin-top: -20px;">
                    <v-btn 
                    outlined 
                    rounded 
                    class="text-xs-center"
                    @click="getEvent(mainEvent.id)"
                >Перейти</v-btn>
                </v-card-text>
            
            </v-card>
        </div>
        
        <v-expansion-panels
            v-else 
            multiple
        >
        <v-expansion-panel v-for="mainEvent in events" :key="mainEvent.id">
            <v-expansion-panel-header>{{ mainEvent.mainTitle }}</v-expansion-panel-header>
            <v-expansion-panel-content>
            <v-img :src="'/images/' + mainEvent.image"></v-img>
            <v-card-text>{{ mainEvent.mainDesc }}</v-card-text>

            <v-btn 
                outlined 
                rounded 
                class="text-xs-center"
                @click="getEvent(mainEvent.id)"
            >Перейти</v-btn>
            </v-expansion-panel-content>
        </v-expansion-panel>
        </v-expansion-panels>
    </v-container>

</template>

<script>
import axios from 'axios'
    export default {
        data() {
            return {
                events: [],
                date: [],
                dateFilt: [],
                show: true
            }
        },

        computed: {
            getUrl() {
                return this.$store.state.url
            }
        },

        async mounted() {

            if (!this.$store.state.user || Object.keys(this.$store.state.user).length == 0) {
            this.$fire({
                title: "Уведомление",
                text: "Необходимо авторизоваться!",
                type: "error",
            })
            .then(this.$router.push('/howtouse'));
        }

            console.log("URL " + this.$store.state.getUrl)

            document.title = 'Технополис "ЭРА" | Программа';
            this.$emit('update:title', 'Программа')

            await axios.get(this.$store.state.getUrl + 'api/getEvents')
            .then(async res => {
                console.log(res.data)
                this.events = await res.data
            })

            this.sortEvents()

            this.events.forEach(elem => {
                this.date.push(JSON.stringify({
                    date: new Date(elem.timeStart).toLocaleDateString(),
                    events: []
                }))
            })

            this.date = Array.from(new Set(this.date))

            this.date.forEach(elem => {
                this.dateFilt.push(JSON.parse(elem))
            })

            this.addEvents()
            this.getBlue()

            console.log(this.dateFilt)
        },

        methods: {

            async getEvent(id) {
                this.$router.push({ path: `/event/${id}` })
            },

            sortEvents() {
                this.events = this.events.sort(function (a, b) {
                    if (a.timeStart > b.timeStart) {
                        return 1;
                    }
                    if (a.timeStart < b.timeStart) {
                        return -1;
                    }
                    return 0;
                });
            },

            getBlue() {
          for (let i = 0; i < this.events.length; i++) {
              if (Number(new Date(this.events[i].timeStart)) < Date.now() && Number(new Date(this.events[i].timeStop)) > Date.now()) {
                  this.events[i].isActive = 1
              }
              else if (Number(new Date(this.events[i].timeStop)) < Date.now()) {
                this.events[i].isActive = 2;
              }
              else if (Number(new Date(this.events[i].timeStart)) > Date.now()) {
                this.events[i].isActive = 0;
              }
          }
      },

            addEvents() {
                for (let i = 0; i < this.events.length; i++) {
                    for(let j = 0; j < this.dateFilt.length; j++) {
                        if (this.dateFilt[j].date == new Date(this.events[i].timeStart).toLocaleDateString()) {
                            this.dateFilt[j].events.push(this.events[i])
                        }
                    }
                }
            },

            getWeekDay(date) {
                let days = ['ВС', 'ПН', 'ВТ', 'СР', 'ЧТ', 'ПТ', 'СБ'];
                return days[new Date(date).getDay() + 1];
            }
        }
    }
</script>

<style scoped>
    .isNow {
        background: #d5e6a2;;
    }

    .v-expansion-panel-content__wrap {
        padding: 0px !important;
    }
</style>